import React from 'react';
import {CProgress} from '@coreui/react';

export default ()=>{
  /*example start: script*/
  let counter = 86;
  /*example end: script*/
  /*example start: template*/
  return (
    <div>
      <CProgress
        value={counter}
        animated
        showValue
        height="30px"
      />
      <CProgress custom className="mt-1">
        <CProgress
          bar
          showValue
          value={counter*(6/10)}
          color="success"
          height="30px"
        />
        <CProgress
          bar
          showValue
          value={counter*(2.5/10)}
          color="warning"
        />
        <CProgress
          bar
          showValue
          value={counter*(1.5/10)}
          color="danger"
        />
      </CProgress>
    </div>
  )
  /*example end: template*/
}
