import React from 'react';
import {CDropdown, CDropdownItem} from '@coreui/react';

export default ()=>{
  /*example start: template*/
  return (
    <div>
      <CDropdown
      color="primary"
      toggler-text="Dropdown Button"
      className="m-2"
      >
        <CDropdownItem>First Action</CDropdownItem>
        <CDropdownItem>Second Action</CDropdownItem>
        <CDropdownItem>Third Action</CDropdownItem>
        <CDropdownItem divider></CDropdownItem>
        <CDropdownItem>Something else here...</CDropdownItem>
        <CDropdownItem disabled>Disabled action</CDropdownItem>
      </CDropdown>
    </div>
  )
  /*example end: template*/
}
